.logo {
    color: burlywood;
    font-size: 40px;
    font-weight: bolder;
    font-style: oblique;
    justify-content:right;
  }
  .pages ul li {
    font-size: 30px;
    justify-content:right;
  }
  nav {
    background-image:url('https://th.bing.com/th/id/OIP.TSNJ15TenJrnCDAfWXPaKwHaEo?w=248&h=180&c=7&r=0&o=5&pid=1.7');
    padding: 20px 15px;
    display: flex;
    justify-content:space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display:inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 20px;
  }
  
  a {
    color: burlywood;
    text-align: center;
  }
  
  a:hover {
    color: brown;
  }
  
  /* Page Content */
  .container {
    max-width: 800px;
    margin: 20px auto;
    padding: 0 20px;
  }
  
  h2 {
    color: black;
  }
  
  p {
    color: black;
  }
  
  .home h2 {
    color:burlywood;
  }
  
  .contact h2 {
    color: burlywood;
  }
  
  .about h2 {
    color: burlywood;
  }
  
  .name {
    color:black;
    font-size: 20px;
  }

